import { Component, OnInit, Input, Output, EventEmitter, ElementRef, AfterViewInit, ViewChild, HostBinding } from '@angular/core';
import { PaginationControlsComponent, PaginationControlsDirective } from '@farris/ui-pagination';
import { OverLayHiddenService } from '@farris/ui-common';

@Component({
    selector: 'datagrid-pager',
    templateUrl: './page.component.html'
})
export class DatagridPagerComponent implements OnInit, AfterViewInit, OnChanges {
    @Input() pagerPos = 'right';
    @Input() id = 'farris-datagrid-pager';
    /** æ¾ç¤ºé¡µç çæ°é */
    @Input() maxSize = 7;
    /** æ¯å¦æ¾ç¤ºé¡µç  */
    @Input() directionLinks = true;

    @Input() autoHide = false;

    @Input() responsive = false;
    /** ä¸é¡µæ ç­¾ */
    @Input() previousLabel = 'ä¸é¡µ';
    /** ä¸é¡µæ ç­¾ */
    @Input() nextLabel = 'ä¸é¡µ';
    @Input() showPageList = false;
    @Input() showPageInfo = true;
    @Input() showPageNumber = true;
    @Input() locked = false;

    @Input() selectedData = [];
    @Input() viewMode: 'default' | 'simple' = 'default';
    @Input() listItemFormatter: (data) => string;

    @Input() showSelectedList = false;

    @Input() showGotoInput = false;

    /** åé¡µæ¡èªå®ä¹åå®¹åºæ¨¡æ¿ */
    @Input() contentTemplate: TemplateRef<any> = null;
    @Input() data = null;

    @Output() pageChange = new EventEmitter();
    @Output() pageSizeChange = new EventEmitter();

    @Output() removeSelectedItem = new EventEmitter();
    @Output() clearSelectedItem = new EventEmitter();

    @ViewChild('pagerContainer') pc: ElementRef;
    @ViewChild('pager') pager: PaginationControlsComponent;
    @ViewChild('selectItemPanel') selectItemPanel: ElementRef;

    get pagination(): PaginationControlsDirective {
        return this.pager.paginationDirective;
    }


    get pageSize() {
        return this.pagination.getPageSize();
    }

    get total() {
        return this.pagination.getTotalItems();
    }

    get outerHeight() {
        if (this.pc) {
            return this.pc.nativeElement.offsetHeight;
        }
        return 40;
    }
    styles = { opacity: 1 };

    mouseInSelectedList = false;
    openSelectedList = false;

    private selectionDataChange$: Subscription = null;

    private notifySer: NotifyService;
    private overLayService: OverLayHiddenService = null;
    constructor(public el: ElementRef, private cd: ChangeDetectorRef, @Optional() private inject: Injector) {
        if (this.inject) {
            this.notifySer = this.inject.get<NotifyService>(NotifyService, null);
        }

        this.overLayService = new OverLayHiddenService();
    }

    ngOnInit(): void {
        this.listenCheckDataChanged();
    }

    ngAfterViewInit() {
        this.lockedStyles();
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.selectedData && !changes.selectedData.isFirstChange()) {
            if (!this.selectedData || !this.selectedData.length) {
                this.closeSelectedList();
            }
        }

        if (changes.showSelectedList && !changes.showSelectedList.isFirstChange()) {
            this.listenCheckDataChanged();
        }

        if (changes.locked && !changes.locked.isFirstChange()) {
            this.lockedStyles();
        }
    }

    private listenCheckDataChanged() {
        if (this.showSelectedList) {
            const dg = this.inject.get(DatagridComponent, null);
            if (dg) {
                this.selectionDataChange$ = dg.checkedChange.subscribe((data: any) => {
                    this.selectedData = data;
                    if (!this.cd['destroyed']) {
                        this.cd.detectChanges();
                    }
                });

                this['resized$'] = dg.resized.subscribe(() => {
                    this.closeSelectedList();
                })
            }
        } else {
            if (this.selectionDataChange$) {
                this.selectionDataChange$.unsubscribe();
                this.selectionDataChange$ = null;
            }
        }
    }

    onPageChange(pageIndex: any) {
        if (typeof pageIndex === 'object') {
            pageIndex = pageIndex.pageIndex || 1;
        }
        this.pageChange.emit(Number.parseInt(pageIndex, 10));
    }

    onPageSizeChange(pageSize: any) {
        this.pageSizeChange.emit(Number.parseInt(pageSize, 10));
    }

    lock() {
        this.locked = true;
        this.lockedStyles();
    }
    unlock() {
        this.locked = false;
        this.lockedStyles();
    }

    private lockedStyles() {
        this.styles = { opacity: this.locked? 0.5 : 1 };
        this.cd.detectChanges();
    }

    onMouseEnter($event) {
        $event.stopPropagation();

        if (this.locked) {
            return;
        }
        this.mouseInSelectedList = true;
        if (this.selectedData && this.selectedData.length) {
            this.openSelectedList = !this.openSelectedList;
        } else {
            this.openSelectedList = false;
        }

        let target = $event.target;
        if (!target.classList.contains('selected-list-badge')) {
            target = $event.target.closest('.selected-list-badge');
        }


        if (this.openSelectedList) {

            const selectedListContainer = this.el.nativeElement.querySelector('.pager-overlay-container');
            const isInModal = target.closest('.modal-dialog');

            let { top, left } = target.getBoundingClientRect();
            if (selectedListContainer) {
                if (isInModal) {
                    const { top: mtop, left: mleft } = isInModal.getBoundingClientRect();
                    top = top - mtop;
                    left = left - mleft;
                }

                selectedListContainer.style.top = `${top}px`;
                selectedListContainer.style.left = `${left - 10}px`;
            }

            this.overLayService.registerMouseEvent(this.selectItemPanel.nativeElement, (e) => {
                if (this.selectItemPanel.nativeElement.contains(e.target) || e.target.classList.contains('selected-list-badge') || e.target.closest('.selected-list-badge')) {
                    return;
                }

                this.closeSelectedList();
                this.overLayService.destory(this.selectItemPanel.nativeElement);
            });
        }
    }


    closeSelectedList() {
        this.openSelectedList = false;
        this.mouseInSelectedList = false;
        if (!this.cd['destroyed']) {
            this.cd.detectChanges();
        }
    }

    onDelSelectedItem(id: any) {
        this.removeSelectedItem.emit(id);
    }

    onClearSelected($event) {
        this.clearSelectedItem.emit();
        this.closeSelectedList();
    }

    onSelectListPanelClose($event) {
        this.closeSelectedList();
    }
}
