import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewEncapsulation } from '@angular/core';

@Component({
    selector: 'scrollbar-arrows',
    styleUrls: [
        './scrollbar-arrows.css'
    ],
    templateUrl: './scrollbar-arrows.component.html',
    encapsulation: ViewEncapsulation.None
})

export class ScrollbarArrowsComponent implements OnInit, OnChanges {

    @Input() reachX: 'start'|'end' = 'start';
    @Input() reachY: 'start'|'end' = 'start';


    @Input() scrollbarXActive = false;
    @Input() scrollbarYActive = false;

    @Output() hasXScrollbar = new EventEmitter();
    @Output() hasYScrollbar = new EventEmitter();

    @Output() scrollMoveTo = new EventEmitter();

    constructor() { }

    ngOnInit() { }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.scrollbarYActive && !changes.scrollbarYActive.isFirstChange()) {
            this.hasYScrollbar.emit(this.scrollbarYActive);
        }
    }


    scrollTo($event: MouseEvent, dir: 'left'|'right'|'top'|'bottom') {
        $event.stopPropagation();
        this.scrollMoveTo.emit({ dir, event: $event });
    }

}