
import { Directive, NgZone, Injector, Renderer2, ElementRef, AfterViewInit, Input, OnChanges, SimpleChanges, OnDestroy } from '@angular/core';
import { dropHandlers, smoothDnD as SmoothDnDForHeader, DropResult, ContainerOptions, constants } from '@farris/smooth-dnd';
import { DatagridComponent } from '../../datagrid.component';
import { DatagridHeaderComponent } from './datagrid-header.component';
import { DragDropColumnService } from './drag-drop-column.service';


const { wrapperClass, animationClass } = constants;
SmoothDnDForHeader.dropHandler = dropHandlers.reactDropHandler().handler;
SmoothDnDForHeader.wrapChild = false;

export interface DragStartEndInfo {
    isSource: boolean;
    payload: any;
    willAcceptDrop: boolean;
}

@Directive({
    selector: '[drag-column]',
    providers: [
        DragDropColumnService
    ]
})
export class DragColumnDirective implements AfterViewInit, OnChanges, OnDestroy {
    private container: any;
    @Input('drag-column') enableDrag = true;
    @Input() groupName = '';

    @Input() options: ContainerOptions = {
        groupName: this.groupName,
        orientation: 'horizontal',
        behaviour: 'move',
        dragHandleSelector: '.drag-column-bar',
        dragClass: 'drag-column-moving',
        dropPlaceholder: {
            className: 'drop-group-field',
        },
        getGhostParent: () => {
            return document.body;
        },
        getChildPayload: this.getChildPayload.bind(this),
        shouldAcceptDrop: (sourceContainerOptions, payload) => {
            if (typeof payload === 'number') {
                return false;
            }

            if (sourceContainerOptions.groupName !== this.groupName) {
                return false;
            }

            return !this.dg.isMultiHeader();
        },
        onDropReady: (dropResult: DropResult) => {
            this.ngzone.run(() => {
                this.onDropReady(dropResult);
            });
        },
        onDrop: (dropResult: DropResult) => {
            this.ngzone.run(() => {
                this.onDrop(dropResult);
            });
        },
        onDragEnter: () => {
            this.ngzone.run(() => {
                this.onDragEnter();
            });
        },
        onDragStart: (info: DragStartEndInfo) => {
            this.ngzone.run(() => {
                this.onDragStart(info);
            });
        },
        onDragEnd: (info: DragStartEndInfo) => {
            this.ngzone.run(() => {
                this.onDragEnd(info);
            });
        }
    };

    constructor(private ngzone: NgZone, private injector: Injector, private render: Renderer2,
                private el: ElementRef, private header: DatagridHeaderComponent, private dg: DatagridComponent,
                private dndSer: DragDropColumnService) { }


    ngOnChanges(changes: SimpleChanges) {
        if (changes.enableDrag && !changes.enableDrag.isFirstChange()) {
            if (this.enableDrag) {
                this.initDnD();
            } else {
                this.disposeDnd();
            }
        }
    }

    ngAfterViewInit() {
        this.initDnD();
    }

    ngOnDestroy() {
        this.disposeDnd();
    }

    private disposeDnd() {
        if (this.container) {
            this.container.dispose();
            this.container = null;
        }
    }

    private initDnD() {
        if (!this.dg.enableDragColumn) {
            return;
        }
        if (this.groupName === 'left' || this.groupName === 'right') {
            const { left, right } = this.columnsGroup();

            if (this.groupName === 'left' && !left.length) {
                return;
            }

            if (this.groupName === 'right' && (!right.length || (right.length === 1 && right[0].field === '_datagrid-setting-control_'))) {
                return;
            }
        }

        this.options.groupName = this.groupName;
        this.disposeDnd();


        if (this.enableDrag) {
            this.container = SmoothDnDForHeader(
                this.el.nativeElement,
                this.options
            );

            this.dndSer.backupColumns(this.header.columns);
        }
    }

    private getChildPayload(index) {
        if (this.groupName === 'left') {
            if (this.dg.showCheckbox) {
                index--;
            }
            if (this.dg.showLineNumber) {
                index--;
            }
        }

        const groupColumns = this.columnsGroup();

        if (this.groupName === 'left') {
            return groupColumns.left.filter((n, i) => this.dg.columnIsVisible(n))[index];
        } else if (this.groupName === 'right') {
            return groupColumns.right.filter((n, i) => this.dg.columnIsVisible(n))[index];
        } else {
            return groupColumns.middle.filter((n, i) => this.dg.columnIsVisible(n))[index];
        }
    }

    onDropReady(dropResult) {
    }

    private onDrop(dropResult: DropResult) {
        const { addedIndex, payload, removedIndex } = dropResult;
        if (addedIndex === null) {
            return;
        }

        const groupColumns = this.columnsGroup();

        const cols = this.dg.columns[0];
        const currIndex = cols.findIndex(n => n.field === payload.field);

        let newIndex = addedIndex;

        const moveColumn = (columns: any) => {
            if (columns) {
                const targetColumn = columns.filter(n => n.visible || n.visible === undefined)[newIndex];
                if (targetColumn) {
                    const realTagetIndex = cols.findIndex(n => n.field === targetColumn.field);
                    this.dg.columns[0] = this.dndSer.moveItem(this.dg.columns[0], realTagetIndex, currIndex);

                    return true;
                }
            }

            return false;
        };

        let columns = [];
        if (this.groupName === 'left') {
            if (this.dg.showCheckbox) {
                newIndex--;
            }
            if (this.dg.showLineNumber) {
                newIndex--;
            }
            columns = groupColumns.left;
        } else if (this.groupName === 'center') {
            columns = groupColumns.middle;
        } else if (this.groupName === 'right') {
            columns = groupColumns.right;
        }

        if (moveColumn(columns)) {
            this.dg.columnsChanged(false);

            if (this.dg.useControlPanel && this.dg.settingService) {
                this.dg.settingService.saveUserConfig(this.dg.id);
            }

            this.dg.columnMoved.emit({ newColumns: this.dg.columns, grid: this.dg });
        }
    }

    private onDragStart(info) {
    }

    private onDragEnd(info) {
    }


    private onDragEnter() {
    }


    private columnsGroup() {
        const leftColumns = this.dg.columns[0].filter(n => n.fixed === 'left');
        const rightColumns = this.dg.columns[0].filter(n => n.fixed === 'right');
        const middleColumns = this.dg.columns[0].filter(n => n.fixed !== 'right' && n.fixed !== 'left');

        return {
            left: leftColumns,
            right: rightColumns,
            middle: middleColumns
        };
    }
}

