import { DatagridComponent } from './../../datagrid.component';
import { Component, OnInit, Input, ViewChild, ElementRef, Injector, Inject, forwardRef, ChangeDetectorRef, OnDestroy, OnChanges, SimpleChanges } from '@angular/core';
import { DatagridFacadeService } from './../../services/datagrid-facade.service';
import { filter, map, takeUntil } from 'rxjs/operators';
import { IS_GROUP_ROW_FIELD } from '../../services/state';

@Component({
    selector: 'datagrid-checkbox',
    template: ` <div class="custom-control custom-checkbox f-checkradio-single">
        <input type="checkbox" #chk class="custom-control-input" [disabled]="disabled" [checked]="checked">
        <label class="custom-control-label" (click)="handleClick($event)"></label>
    </div>`,
    styles: [
        `
        :host {
            vertical-align: middle;
        }
        :host .custom-checkbox {
            opacity: 1;
            float: none;
            top: 2px;
        }
        `
    ]
})
export class DatagridCheckboxComponent implements OnInit, OnDestroy, OnChanges {
    @Input() rowData: any;
    @Input() rowIndex: any;
    @Input() checked: boolean;
    @Input() disabled: boolean;

    @Input() indeterminate = false;
    @ViewChild('chk') chk: ElementRef;

    private dfs: DatagridFacadeService;
    private cd: ChangeDetectorRef;

    checked$ = null;
    private destroy$ = new Subject();
    subscriptions: Subscription[] = [];
    constructor(private injector: Injector, @Inject(forwardRef(() => DatagridComponent)) public dg: DatagridComponent) {
        this.dfs = this.injector.get(DatagridFacadeService);
        this.cd = this.injector.get(ChangeDetectorRef);
    }

    ngOnInit(): void {
        if (this.indeterminate) {
            this.chk.nativeElement.indeterminate = true;
        }
        this.listenSubjects();
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.checked && !changes.checked.isFirstChange()) {
            this.changeCheckedStatus(changes.checked.currentValue);
        }
    }

    ngOnDestroy() {
        this.destroy$.next();
        this.destroy$.complete();

        if (this.subscriptions && this.subscriptions.length) {
            this.subscriptions.forEach(n => n.unsubscribe());
            this.subscriptions = [];
        }
    }

    private listenSubjects() {

        const rid = this.dfs.primaryId(this.rowData);

        this.subscriptions.push(
            this.dg.checkAll.pipe(
                takeUntil(this.destroy$)
            ).subscribe(() => {
               
                let flag = true;

                if (this.dg.disableRow) {
                    flag = !this.dg.disableRow(this.rowData, this.rowIndex);
                    if (!flag) {
                        flag = this.dg.dfs.isRowChecked(rid);
                    }
                }
                this.changeCheckedStatus(flag);
            })
        );

        this.subscriptions.push(
            this.dg.unCheckAll.pipe(
                takeUntil(this.destroy$)
            ).subscribe((e: any) => {
                if (e && e.length) {
                    if (e.find(n => n[this.dg.idField] === this.rowData[this.dg.idField])) {
                        this.changeCheckedStatus(false);
                    }
                } else {
                    this.changeCheckedStatus(false);
                }
            })
        );

        const _setcheckrows = this.dg.dgs.setCheckedRows.pipe(
            takeUntil(this.destroy$)
        ).pipe(
            filter((r: any) => {
                return r.ids.includes(rid) || r.ids.includes('' + rid);
            }),
            map((r: any) => {
                const flag = r.ids.includes(rid) || r.ids.includes('' + rid);
                if (flag) {
                    return flag && r.checked;
                }
                return false;
            }),
        ).subscribe(r => {
            this.checked = r;
            this.changeCheckedStatus(r);
        });
        this.subscriptions.push(_setcheckrows);

        this.dfs.updateCheckboxState$.pipe(
            takeUntil(this.destroy$),
        ).pipe(
            filter((r: any) => {
                return r.id === rid;
            }),
            map((r: any) => {
                return !!r.checked;
            })
        ).subscribe((r: any) => {
            this.checked = r;
            this.changeCheckedStatus(r);
        });
    }

    handleClick(event: MouseEvent) {
        if (!this.disabled) {
            const beforEventParam = {
                rowIndex: this.rowIndex,
                rowData: this.rowData,
                gridInstance: this.dg
            };
            this.dg.endCellEdit();
            const _checked = this.chk.nativeElement.checked;
            if (!_checked) {
                this.dg.beforeCheck(beforEventParam).subscribe((canCheck: boolean) => {
                    if (canCheck) {
                        this.dfs.checkRow(this.rowIndex, this.rowData, { instance: this.dg});
                        this.checked = true;
                        this.changeCheckedStatus(true);
                    }
                });
            } else {
                this.dg.beforeUncheck(beforEventParam).subscribe((canUncheck: boolean) => {
                    if (canUncheck) {
                        this.dfs.unCheckRow(this.rowIndex, this.rowData, { instance: this.dg});
                        this.checked = false;
                        this.changeCheckedStatus(false);
                        if (this.dg.showSelectedList) {
                            this.dg.cd.detectChanges();
                        }
                    }
                });
            }
            // this.checked = !this.checked;
            // this.cd.detectChanges();
        }
        event.stopPropagation();
    }

    private changeCheckedStatus(status: boolean) {
        this.chk.nativeElement.checked = status;
    }

}
