import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'selectedItem'})
export class SelectedItemPipe implements PipeTransform {
    transform(data: any, formatter: any): any {
        if (data) {
            if (formatter) {
                data = data.map( d => {
                    return {id: d.id, text:  formatter(d.data) };
                });
            } else  {
                data = data.map(d => {
                    const txt = [];
                    const keys = Object.keys(d.data);
                    keys.forEach(k => {
                        txt.push(d.data[k]);
                    });
                    return {id: d.id, text: txt.join(', ') };
                });
            }

            return data;
        }
    }
}
