import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { ColumnFilterType } from '../types/data-column';

import { BooleanFilterControl, DateFilterControl, EnumFilterControl,
        NumberFilterControl, TextFilterControl } from '../plugins/smart-filter/controlData/filter-control.options';

@Injectable()
export class DatagridSmartFilterService {
    private conditions: any[] = [];
    controlData: any = [];

    filterChanged = new Subject();

    removeFilter = new Subject();
    clearAllFilter = new Subject();

    constructor() { }


    getColumnFilterData(col: any) {
        let filterControlData: any = {
            controltype: 'text',
        };
        if (col.filter !== undefined) {
            if (typeof col.filter === 'boolean') {
                if (col.formatter) {
                    if (typeof col.formatter === 'object' && Object.keys(col.formatter).length) {
                        const options: any = col.formatter['options'];
                        switch (col.formatter['type']) {
                            case 'number':
                                return NumberFilterControl(col);
                            case 'enum':
                                return EnumFilterControl(col, options);
                            case 'boolean':
                                return BooleanFilterControl(col);
                            case 'datetime':
                                let tye = 'datetime';
                                let fmt = 'yyyy-MM-dd';
                                let returnFmt = 'yyyy-MM-dd';
                                if (options && options.format) {
                                    fmt = options.format;
                                    if (fmt.indexOf('HH:') == -1) {
                                        tye = 'date';
                                    } else {
                                        returnFmt += ' HH:mm';
                                        if (fmt.indexOf('ss') > -1) {
                                            returnFmt += ':ss';
                                        }
                                    }
                                } else {
                                    tye = 'date';
                                }
                                filterControlData = {
                                    controltype: 'flexibleDate',
                                    dateFormat: fmt,
                                    single: false,
                                    showType: 3,
                                    showTime: tye === 'datetime'
                                };
                                break;
                            default:
                                return TextFilterControl(col);
                        }
                    }
                }
                return filterControlData;
            } else {
                switch (col.filter.type) {
                    case ColumnFilterType.enum:
                        return EnumFilterControl(col, col.filter.options);
                    case ColumnFilterType.date:
                    case ColumnFilterType.datetime:
                        return DateFilterControl(col, col.filter.options);
                    case ColumnFilterType.number:
                        return NumberFilterControl(col);
                    case ColumnFilterType.boolean:
                        return BooleanFilterControl(col);
                }
            }
        }

        return filterControlData;
    }



    filterConditionChanged(e: {conditions: any[], controlData: any, from: 'toolbar' | 'panel'}) {
        const items = e.conditions;

        if (!this.conditions || !this.conditions.length) {
            this.conditions.push(...items);
            this.controlData.push({...e.controlData});
        } else {
            /// TODO, CONTROLDATA çç´¢å¼ä¸conditions çç´¢å¼å¾å¯è½ä¸æ¯1ä¸ª
            if (e.from === 'toolbar') {
                this.conditions = [...items];
            } else {
                this.conditions = this.conditions.filter(n => n.FilterField !== items[0].FilterField);
                if (items.length === 1) {
                    this.conditions.push({ ...items[0] });
                } else {
                    this.conditions.push(...items);
                }
            }

            if (e.controlData) {
                const ctrlIdx = this.controlData.findIndex(n => n.labelCode === e.controlData.labelCode);
                if (ctrlIdx > -1) {
                    this.controlData[ctrlIdx] = {...e.controlData};
                } else {
                    this.controlData.push({...e.controlData});
                }
            }
        }

        this.filterChanged.next({conditions: [...this.conditions], controlData: [...this.controlData] });
    }

    removeCondition(e, emitRemove = false) {
        if (this.conditions && this.conditions.length) {
            this.conditions = this.conditions.filter(n => n.FilterField !== e.labelCode);
            this.controlData = this.controlData.filter(n => n.labelCode !== e.labelCode);
        }
        this.filterChanged.next({conditions: [...this.conditions], controlData: [...this.controlData] });
        if (emitRemove) {
            this.removeFilter.next(e);
        }
    }

    clearAll() {
        this.conditions = [];
        this.controlData = [];
        this.clearAllFilter.next(null);
    }
}
