import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class DragDropColumnService {
    private initColumns;
    constructor() {}

    /** åç´ ä½ç½®è°æ´
     * arr: æ°ç»
     * newIndex: æ°ä½ç½®ç´¢å¼
     * removedIndex: èä½ç½®ç´¢å¼
     */
    moveItem(arr, newIndex, removedIndex) {
        const removedItem = arr.splice(removedIndex, 1);
        arr.splice(newIndex, 0, ...removedItem);
        return arr;
    }

    backupColumns(columns) {
        this.initColumns = columns.map( cols => {
            return cols.map( n => {
                return {
                    id: n.id,
                    field: n.field
                };
            });
        });
    }
}
