import { debounce } from 'rxjs/operators';
import { Component, OnInit, EventEmitter, Output, Input, OnChanges, SimpleChanges, ViewChild, ElementRef } from '@angular/core';
import { interval } from 'rxjs';

@Component({
    selector: 'pager-selected-list',
    templateUrl: './selected-list.component.html',
})
export class PagerSelectedListComponent implements OnInit, OnChanges {

    @Input() data = [];

    @Output() close = new EventEmitter();
    @Output() remove = new EventEmitter();
    @Output() clear = new EventEmitter();


    constructor() { }

    ngOnInit(): void { }

    ngOnChanges(changes: SimpleChanges) {
    }

    onClose($event: MouseEvent) {
        $event.stopPropagation();
        this.close.emit();
    }

    onRemoveClick($event: MouseEvent, id: any) {
        $event.stopPropagation();
        this.remove.emit(id);
    }

    onClearClick($event: MouseEvent) {
        $event.stopPropagation();
        this.clear.emit();
    }

    onClick($event: MouseEvent) {
        $event.stopPropagation();
        return false;
    }

    trackByFn = (index, item) => {
        return item.id;
    }
}
