import { takeUntil } from 'rxjs/operators';
import { Component, OnInit, ChangeDetectorRef, ElementRef, Injector, NgZone, Input, Output, EventEmitter, OnDestroy, OnChanges, SimpleChanges, Renderer2 } from '@angular/core';
import { Subscription, Subject } from 'rxjs';
import { DatagridService } from './../../services/datagrid.service';
import { DatagridComponent } from '../../datagrid.component';
import { DatagridBodyComponent } from './datagrid-body.component';
import { ROW_INDEX_FIELD, IS_GROUP_ROW_FIELD, GROUP_ROW_FIELD, IS_GROUP_FOOTER_ROW_FIELD, GROUP_VISIBLE_FIELD, GROUP_LEVEL_FIELD } from '../../services/state';
import { DatagridFacadeService } from '../../services/datagrid-facade.service';


@Component({
    selector: 'fixed-left-rows',
    templateUrl: 'fixed-left-rows.component.html'
})

export class FixedLeftRowsComponent implements OnInit, OnDestroy {

    @Input() columns = [];
    @Output() toggle = new EventEmitter();
    @Input() checkedRows = [];
    @Input() selectedRows = [];
    @Input() data = [];

    /** å¯ç¨åç»æ¶ï¼æ°æ®æºä¸­èªå¨è®¾ç½®è¡ç´¢å¼å­æ®µ */
    groupRowIndex = ROW_INDEX_FIELD;
    isGroupRow = IS_GROUP_ROW_FIELD;
    groupRow = GROUP_ROW_FIELD;
    isGroupFooter = IS_GROUP_FOOTER_ROW_FIELD;
    visible = GROUP_VISIBLE_FIELD;
    groupLevelField = GROUP_LEVEL_FIELD;

    filterConditions = '';

    subscriptions: Subscription[] = [];

    dgs: DatagridService = null;
    render2: Renderer2;
    private destroy$ = new Subject();
    constructor(
        private cd: ChangeDetectorRef,
        public dg: DatagridComponent,
        public dgb: DatagridBodyComponent,
        public dfs: DatagridFacadeService,
        public el: ElementRef, private injector: Injector, private ngZone: NgZone) {
            this.dgs = this.injector.get(DatagridService);
            this.render2 = this.injector.get(Renderer2);
        }

    private listenSubject() {
        // const _selectrow = this.dfs.selectRow$.subscribe(() => {
        //     this.detectChanges();
        // });
        // this.subscriptions.push(_selectrow);

        // const _unselectrow = this.dfs.unSelectRow$.subscribe(() => {
        //     this.detectChanges();
        // });
        // this.subscriptions.push(_unselectrow);

        const _setselectrow = this.dgs.setSelecedRow.pipe(
            takeUntil(this.destroy$)
        )
        .subscribe((r: any) => {
            this.detectChanges();
        });
        this.subscriptions.push(_setselectrow);

        this.dfs.checkRow$.pipe(
            takeUntil(this.destroy$)
        ).subscribe( () => {
            this.checkedRows = this.dfs.getCheckeds();
            this.detectChanges();
        });

        this.dfs.clearCheckeds$.pipe(
            takeUntil(this.destroy$)
        ).subscribe( () => {
            this.checkedRows = this.dfs.getCheckeds();
            this.detectChanges();
        });
    }

    ngOnInit() {
        this.listenSubject();
    }

    ngOnDestroy() {
        this.destroy$.next();
        this.destroy$.complete();
        if (this.subscriptions && this.subscriptions.length) {
            this.subscriptions.forEach(n => n.unsubscribe());
            this.subscriptions = [];
        }
    }

    private detectChanges() {
        if (!this.cd['destroyed']) {
            // this.cd.markForCheck();
            this.cd.detectChanges();
        }
    }

    toggleGroupRow(row, index, open, $event?: MouseEvent) {
        if ($event) {
            $event.stopPropagation();
        }
        row.expanded = open;
        this.dg.dfs.setGroupRowsVisible(row, open);
    }

    getGroupSummaryData(groupId: string) {
        const _data = this.dg.getRows();
        if (_data && _data.length) {
            const row = this.dg.getRows().find(n =>  n['__group_parent_id__'] === groupId && n['__group_footer__']);
            if (row) {
                return row.data;
            }
        }
        return null;
    }
    
    getGroupColumn(field) {
        return this.dg.flatColumns.find(col => col.field === field);
    }
}
