import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'showStar'})
export class ShowRequiredStarPipe implements PipeTransform {
    transform(validators: any): any {
        if (validators && validators.length) {
            const isRequired = validators.find(v => v.type === 'required');
            return isRequired;
        }
        return false;
    }
}
