export function TextFilterControl(col: any) {
    return {
        controltype: 'text'
    };
}

export function BooleanFilterControl(col: any) {
    return {
        controltype: 'bool-check',
        // enumValues: [
        //     {value: true, name: 'true'},
        //     {value: false, name: 'false'}
        // ]
    };
}

export function EnumFilterControl(col: any, enumopts: any) {
    const valueField = enumopts.valueField || enumopts.idField;
    const data = enumopts.data.map(n => {
        return {
            value:  n[valueField],
            name: n[enumopts.textField]
        };
    });

    return {
        controltype: 'checkboxgroup',
        enumValues: data,
    };
}

export function DateFilterControl(col, options: any) {
    let tye = 'datetime';
    let fmt = 'yyyy-MM-dd';
    let returnFmt = 'yyyy-MM-dd';
    if (options && options.format) {
        fmt = options.format;
        if (fmt.indexOf('HH:') == -1) {
            tye = 'date';
        } else {
            returnFmt += ' HH:mm';
            if (fmt.indexOf('ss') > -1) {
                returnFmt += ':ss';
            }
        }
    } else {
        tye = 'date';
    }

    // all = 1, å¹´ææ¥
    // noDate = 2,  å¹´æ
    // noDateAndMonth = 3,  å¹´

    return {
        controltype: 'flexibleDate',
        dateFormat: fmt,
        single: false,
        showType: 1,
        showTime: tye === 'datetime'
    };
}

export function NumberFilterControl(col) {
    return  {
        controltype: 'flexibleNumber',
        single: false
    };
}
