import { Pipe, PipeTransform } from '@angular/core';
import { TR_PREFIX } from './../types/constant';
import { DatagridFacadeService } from '../services/datagrid-facade.service';

@Pipe({name: 'rowDataId'})
export class RowDataIdPipe implements PipeTransform {

    constructor(private dfs: DatagridFacadeService) {}

    transform(rowData: any, fixedRight = false, fixedLeft = false, isGroupRow = false): any {
        if (rowData) {
            let rowID = TR_PREFIX + this.dfs.primaryId(rowData);
            if (fixedRight) {
                rowID += '_fixedright';
            } else if (fixedLeft) {
                rowID += '_fixedleft';
            }

            if (isGroupRow) {
                rowID += '_grouprow';
            }

            return rowID;
        }
        return null;
    }
}
