import { Directive, ElementRef, Renderer2, Injector, Input, OnInit, AfterViewInit } from '@angular/core';
import { DatagridComponent } from '../../datagrid.component';

@Directive({
    selector: '[group-footer-row-styler]',
})
export class GroupFooterRowStylerDirective implements OnInit, AfterViewInit {
    @Input() column: any;
    @Input('group-footer-row-styler') rowData: any;
    @Input() rowIndex: any;

    constructor(private el: ElementRef, private render: Renderer2, private inject: Injector, public dg: DatagridComponent) { }


    ngOnInit() {
    }

    ngAfterViewInit() {
        this.buildCustomGroupFooterRowStyle(this.rowData);
    }

    private buildCustomGroupFooterRowStyle(rowData) {

        if (this.dg.groupFooterStyler) {

            const cs = this.dg.dgs.getCustomStyle(this.dg.groupFooterStyler, [rowData]);
            if (cs) {
                this.dg.renderCustomCls(cs.cls, this.el.nativeElement);
                if (cs.style) {
                    Object.keys(cs.style).forEach(n => {
                        this.el.nativeElement.style[n] = cs.style[n];
                    });
                }
            }
        }
    }
}
