    // èªå¨é«åº¦æ¶ï¼ç¦ç¨çºµåæ»å¨æ¡
    psConfig = { swipeEasing: false, minScrollbarLength: 15, handlers: ['click-rail', 'drag-thumb', 'wheel', 'touch'], suppressScrollY: this.dg.autoHeight && this.dg.maxHeight ? false: this.dg.autoHeight };

    top: number;
    height: number;
    width: number;
    colsWidth: number;
    totalWidth: number;
    leftFixedWidth: number;
    rightFixedWidth: number;
    rowHeight: number;
    bodyStyle: any;
    scrollTop = 0;
    scrollLeft = 0;
    deltaTopHeight = 0;
    // wheelHeight = 0;
    wheelWidth = 0;
    private _fixedRightScrollLeft = 0;
    get fixedRightScrollLeft() {
        return this._fixedRightScrollLeft;
    }
    set fixedRightScrollLeft(val) {
        this._fixedRightScrollLeft = val;
    }

    maxScrollLeft = 0;
