import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'cellReadonly'
})
export class CellIsReadonlyPipe implements PipeTransform {
    transform(col: any, ...args: any[]): any {
        return this.isReadonly(col, args[0], args[1]);
    }

    private isReadonly(col, rowData, dg) {
        if (dg.editable) {
            // åä¸åå¹¶çååæ ¼ä¸åè®¸ç¼è¾
            if (dg.mergeCell && dg.mergeFields.indexOf(col.field) > -1) {
                return true;
            }
            return this.getReadonlyValue(col, rowData);
        } else {
            return true;
        }
    }

    private getReadonlyValue(col, rowData) {
        if (col.readonly !== undefined) {
            if (typeof col.readonly === 'boolean') {
                return col.readonly;
            } else if (typeof col.readonly === 'function') {
                return col.readonly(rowData);
            }
        }
        return false;
    }
}
