import { Pipe, PipeTransform } from '@angular/core';
import { DatagridFacadeService } from './../services/datagrid-facade.service';

@Pipe({name: 'rowIsChecked', pure: false})
export class RowIsCheckedPipe implements PipeTransform {
    constructor(private dfs: DatagridFacadeService) {}
    transform(rowData: any, checkeds: any[]): any {
        if (!rowData) {
            return false;
        }

        const id = this.dfs.primaryId(rowData);
        if (id) {
            return this.dfs.isRowChecked(id, checkeds);
        } else {
            return false;
        }
    }
}
