import { Pipe, PipeTransform } from '@angular/core';
import { DatagridFacadeService } from '../services/datagrid-facade.service';

@Pipe({name: 'rowIsSelected', pure: false})
export class RowIsSelectedPipe implements PipeTransform {
    constructor(private dfs: DatagridFacadeService) {}
    transform(rowData: any, selections?: any[]): any {
        if (!rowData) {
            return false;
        }
        const id = this.dfs.primaryId(rowData);

        if (id) {
            return this.dfs.isRowSelected(id, selections);
        } else {
            return false;
        }
    }
}
