    private _loading = false;
    get loading() {
        return this._loading;
    }
    set loading(val: boolean) {
        this._loading = val;
        if (!this.cd['destroyed']) {
            this.cd.detectChanges();
        }
    }

    get selections(): SelectedRow[] {
        return this.dfs.getSelections();
    }

    get checkeds() {
        return this.checkedRows;
    }

    get checkedRows() {
        const chkeds = this.dfs.getCheckeds();
        if (this.enableMorePageSelect) {
            if (chkeds && chkeds.length) {
                if (!this.checkedRowsDataFromSource) {
                    return [...chkeds];
                }
                // ä»æ°æ®æºä¸­è·åææ°æ°æ®
                return chkeds.map(n => {
                    const d = this.dfs.findRow(n.id);
                    if (d) {
                        n.data = d.data;
                    }
                    return n;
                });
            }
        } else {
            if (this.data && this.data.length) {
                const temp = this.data.map(n => n[this.idField]);
                if (!this.checkedRowsDataFromSource) {
                    return chkeds.filter(n => temp.includes(n.id));
                }
                // ä»æ°æ®æºä¸­è·åææ°æ°æ®
                return chkeds.filter(n => temp.includes(n.id)).map(n => {
                    const d = this.dfs.findRow(n.id);
                    if (d) {
                        n.data = d.data;
                    }
                    return n;
                });
            }
        }
        return [];
    }

    set checkedRows(rows: any[]) {
        if (rows && rows.length) {
            rows = rows.map(r => {
                if (r.data) {
                    return r;
                }
                return {
                    id: r[this.idField],
                    data: r
                };
            });
        }

        this.dfs.updateProperty('checkedRows', rows || []);
        this.initSelectOrChecked();
        this.cd.markForCheck();
    }

    private _selectedRow = null;
    get selectedRow(): SelectedRow {
        return this._selectedRow;
    }
    set selectedRow(row) {
        if (row) {
            if (this.selectValue != row.id) {
                this._selectedRow = row;
                this.selectValue = row.id;
                this.selectValueChange.emit(row.id);
                this.selectChanged.emit(row);
            }
        } else {
            this.unSelect.emit(this._selectedRow);
            this.selectValue = null;
            this.selectValueChange.emit('');
            this._selectedRow = null;
        }
        // this.cd.detectChanges();
    }

