import { DataColumn, ColumnFilterType } from './../types/data-column';
import { bindCallback, of } from 'rxjs';

export class DatagridColumnsHelper {

    static gridInstance = null;

    /** è·åæ»åæ° */
    private static getColumnTotal(firstCols: any[]) {
        let total = 0;
        firstCols.forEach(n => {
            total += (n.colspan || 1);
        });
        return total;
    }

    // æ¥æ¾æªèµå¼é¡¹çç´¢å¼
    private static findUndefinedItem(arr) {
        for (let i = 0; i < arr.length; i++) {
            if (arr[i] == undefined) {
                return i;
            }
        }
        return -1;
    }

    private static getColumnFields(columns) {
        const aa = [];
        // æ ¹æ®ç¬¬ä¸è¡è®¡ç®æ»åæ°
        const colTotal = DatagridColumnsHelper.getColumnTotal(columns[0]);
        // åå»ºç©ºæ°ç»
        for (let i = 0; i < columns.length; i++) {
            aa[i] = new Array(colTotal);
        }
        for (let i = 0; i < columns.length; i++) {
            columns[i].forEach(col => {
                let k = DatagridColumnsHelper.findUndefinedItem(aa[i]);
                if (k >= 0) {
                    const field = col.field || col.id || '';
                    for (let c = 0; c < (col.colspan || 1); c++) {
                        for (let r = 0; r < (col.rowspan || 1); r++) {
                            aa[i + r][k] = field;
                        }
                        k++;
                    }
                }
            });
        }
        return aa;
    }

    static columnIsVisible(col: any, ins: any) {
        if (col.visible === undefined) {
            col.visible = true;
        }

        const t = typeof col.visible;
        if (t === 'boolean') {
            return col.visible;
        } else if ( t === 'function') {
            return col.visible({instance: ins, column: col});
        } else {
            return true;
        }
    }

    static getFields(columns) {
        // ç§»é¤éèçå
        const _cols = columns.map(cols => {
            cols = cols.filter(col => DatagridColumnsHelper.columnIsVisible(col, DatagridColumnsHelper.gridInstance));
            return cols;
        });

        const fields =  DatagridColumnsHelper.getColumnFields(_cols);
        return fields.length ? fields[fields.length - 1] : fields;
    }

    static getFilterEnumOptions(column: DataColumn) {
        let enumSetting = null;
        if (column.filter.type === ColumnFilterType.enum) {
            const fmt = column.formatter as any;
            if (fmt) {
                enumSetting = fmt.options;
            } else {
                if (column.filter.options) {
                    enumSetting = column.filter.options;
                }
            }
        }

        return enumSetting;
    }

    static getEnumText(enumOpts: any, value1: any) {
        if (!enumOpts) {
            return  '';
        }
        const { valueField, textField, data } = enumOpts;
        let str = '';
        if (value1) {
            str += ` ${value1.map(v => {
                const enumItem = data.find(d => d[valueField] === v);
                return enumItem[textField];
            }).join(',')}`;
        }
        return str;
    }

    static columnVisibleAsObservable(col: DataColumn) {
        if (typeof col.visible === 'function') {
            const visibleAsObservable = bindCallback(col.visible);
            return visibleAsObservable();
        }
        return of(col.visible);
    }

    static getParentColumns(col: DataColumn, allCols) {
        const arr = [];
        const _getParents = (c) => {
            if (c && c.parentId) {
                const parentCol = allCols.find(n => n.id === c.parentId);
                // arr.splice(0, 0, parentCol);
                arr.push(parentCol);
                _getParents(parentCol);
            }
        };
        _getParents(col);
        return arr;
    }

    static getChildColumns(col: DataColumn, allCols) {
        const arr = [];
        const _getChilds = (c) => {
            if (c && c.parentId) {
                const childs = allCols.filter(n => n.parentId === c.id);
                arr.splice(0, 0, ...childs);
                childs.forEach(child => {
                    _getChilds(child);
                });
            }
        };
        _getChilds(col);
        return arr;
    }

    // æ£æ¥å¤è¡¨å¤´æ¶ï¼è®¾è®¡å¨ä¸­å°åè®¾ä¸ºéè
    static checkGroupHeaderColumnVisible(allCols) {
        const hiddenFields = allCols.filter(n => n.visible === false).map(n => n.field);
        hiddenFields.forEach(field => {
            const _col = allCols.find(col => col.field === field || col.id === field);
            if (_col) {
                const parents = DatagridColumnsHelper.getParentColumns(_col, allCols);

                parents.forEach(p => {
                    const _childs = allCols.filter(n => n.parentId === p.id && (n.visible || n.visible === undefined) );
                    p.colspan = _childs.reduce(( r, c ) => {
                        r = r + (c.colspan || 1);
                        return r;
                    }, 0);
                    p.visible = !!p.colspan;
                });
            }
        });
    }

    // å¤è¡¨å¤´ä¸ä»æä¸åæ¾ç¤º
    static onlyOneColumn() {

    }
}
