
import { Directive, NgZone, Injector, Renderer2, ElementRef, AfterViewInit, Input, OnDestroy } from '@angular/core';
import { dropHandlers, smoothDnD, DropResult, ContainerOptions, constants } from '@farris/smooth-dnd';
import { DatagridComponent } from './../../datagrid.component';
import { DragStartEndInfo } from './datagrid-drag-column.directive';
import { DragDropColumnService } from './drag-drop-column.service';


smoothDnD.dropHandler = dropHandlers.reactDropHandler().handler;
smoothDnD.wrapChild = false;
const { wrapperClass, animationClass } = constants;

@Directive({
    selector: '[drop-column]',
    providers: [
        DragDropColumnService
    ]
})
export class DropColumnDirective implements AfterViewInit, OnDestroy {
    @Input() options: ContainerOptions = {
        orientation: 'horizontal',
        behaviour: 'move',
        dragHandleSelector: '.group-field',
        dropPlaceholder: {
            className: 'drop-group-field',
        },
        getGhostParent: () => {
            return document.body;
        },
        shouldAcceptDrop: (sourceContainerOptions, payload) => {
            if(payload === undefined || payload === null) {
                return false;
            }
            if (typeof payload === 'number') {
                return true;
            }

            if (this.getGroupFields().length < 3) {
                return payload.allowGrouping === undefined || payload.allowGrouping;
            }
            return false;
        },
        getChildPayload: (index) => {
            return index;
        },
        // dragClass: 'drag-column-moving',
        onDropReady: (dropResult: DropResult) => {
            this.ngzone.run(() => {
                this.onDropReady(dropResult);
            });
        },
        onDrop: (dropResult: DropResult) => {
            this.ngzone.run(() => {
                this.onDrop(dropResult);
            });
        },
        onDragEnter: () => {
            this.ngzone.run(() => {
                this.onDragEnter();
            });
        },
        onDragStart: (info: DragStartEndInfo) => {
            this.ngzone.run(() => {
                this.onDragStart(info);
            });
        },
        onDragEnd: (info: DragStartEndInfo) => {
            this.ngzone.run(() => {
                this.onDragEnd(info);
            });
        }
    };

    private container: any;
    constructor(private ngzone: NgZone, private injector: Injector, private render: Renderer2, private el: ElementRef,
                private dg: DatagridComponent, private dndSer: DragDropColumnService) { }

    ngAfterViewInit() {
        this.initDnD();
    }

    ngOnDestroy() {
        this.disposeDnd();
    }

    private disposeDnd() {
        if (this.container) {
            this.container.dispose();
            this.container = null;
        }
    }

    private initDnD() {
        this.disposeDnd();
        if (this.dg.showRowGroupPanel) {
            this.container = smoothDnD(
                this.el.nativeElement,
                this.options
            );
        }
    }

    private onDropReady(dropResult) {
    }

    private getGroupFields() {
        return this.dg.groupField ? this.dg.groupField.split(',') : [];
    }


    private onDrop(dropResult: DropResult) {
        const { addedIndex, payload, removedIndex } = dropResult;

        if (addedIndex === null) {
            return;
        }

        const newGroupFields = this.getGroupFields();

        if (removedIndex === null) {
            if (!newGroupFields.some((v) => v === payload.field)) {
                // newGroupFields.splice(0, 0, payload.field);
                newGroupFields.push(payload.field);
            }
        } else {
            this.dndSer.moveItem(newGroupFields, addedIndex, removedIndex);
        }
        this.dg.groupField = newGroupFields.join(',');
        // this.dg.toggleVisibleColumn([this.dg.groupField], false);

        if (this.dg.useControlPanel && this.dg.settingService) {
            this.dg.checkSettingHttp();
            this.dg.settingService.saveUserConfig(this.dg.id).subscribe( () => {
                this.dg.columnsChanged();
            });
        } else {
            this.dg.columnsChanged();
        }

        this.dg.groupFieldChange.emit({ newGroupField: this.dg.groupField, grid: this.dg });
    }

    private onDragStart(info) {
    }

    private onDragEnd(info) {
    }


    private onDragEnter() {
    }
}
