import { Component, OnInit, Input, ViewChild, ElementRef, AfterViewInit, Injector, Inject, forwardRef } from '@angular/core';
import { ChangeDetectorRef, NgZone, OnDestroy } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { DataColumn, SCROLL_X_ACTION, TR_ID_PREFIX } from '../../types';
import { DatagridComponent } from '../../datagrid.component';
import { DatagridBodyComponent } from './datagrid-body.component';
import ResizeObserver from 'resize-observer-polyfill';
import { IS_GROUP_ROW_FIELD, GROUP_ROW_FIELD, IS_GROUP_FOOTER_ROW_FIELD, GROUP_VISIBLE_FIELD, GROUP_LEVEL_FIELD } from '../../services/state';
import { delay, takeUntil } from 'rxjs/operators';
import { Utils } from '../../utils/utils';

@Component({
    selector: 'datagrid-rows',
    templateUrl: './datagrid-rows.component.html',
    // changeDetection: ChangeDetectionStrategy.OnPush
})
export class DatagridRowsComponent implements OnInit, AfterViewInit, OnDestroy {

    @Input() startRowIndex: number;
    @Input() data: any;
    @Input() columns: DataColumn[];
    @ViewChild('tableEl') tableEl: ElementRef;
    /** æ¯å¦ä¸ºå³ä¾§åºå®åãé»è®¤ä¸º false */
    @Input() fixedRight = false;
    /** æ¯å¦æå·¦ä¾§åºå®å */
    @Input() hasFixedColumns = false;

    @Input() width: number;

    isGroupRow = IS_GROUP_ROW_FIELD;
    /** åç»è¡ç¶IDå­æ®µ */
    groupRow = GROUP_ROW_FIELD;
    isGroupFooter = IS_GROUP_FOOTER_ROW_FIELD;
    visible = GROUP_VISIBLE_FIELD;
    groupLevelField = GROUP_LEVEL_FIELD;

    filterConditions = '';


    get groupSummaryDataInGroupRow() {
        return this.dg.groupSummaryPosition === 'inGroupRow';
    }

    checkedRows = [];

    private ro: ResizeObserver = null;
    private scrollToCurrentRowSubscription: Subscription;
    private subscriptions: Subscription[] = [];
    private destroy$ = new Subject();
    constructor(
        public cd: ChangeDetectorRef,
        @Inject(forwardRef(() => DatagridComponent)) public dg: DatagridComponent,
        @Inject(forwardRef(() => DatagridBodyComponent)) public dgb: DatagridBodyComponent,
        public el: ElementRef, private injector: Injector, private ngZone: NgZone) {

    }

    private listenEvent() {
        const _s3 = this.dg.dgs.setSelecedRow.pipe(takeUntil(this.destroy$)).subscribe((r: any) => {
            this.detectChanges();
        });
        this.subscriptions.push(_s3);

        const _s4 = this.dg.dfs.rowAppend$.pipe(takeUntil(this.destroy$)).subscribe(() => {
            this.detectChanges();
        });
        this.subscriptions.push(_s4);

        const _s5 = this.dg.dfs.rowUpdate$.pipe(takeUntil(this.destroy$)).subscribe((row: { index: number; data: any; }) => {
            if (row) {
                if (this.dg.groupRows && this.dg.groupFooter && this.dg.groupField) {
                    let groupRowId = row.data[this.groupRow];
                    if (groupRowId === undefined) {
                        const _row = this.dg.getRows().find(n => n.id === this.dg.dfs.primaryId(row.data));
                        if (_row) {
                            groupRowId = this.dg.commonUtils.getValue(this.groupRow, _row);
                        }
                    }
                    if (groupRowId !== '' && groupRowId !== null) {
                        this.dg.dfs.updateGroupFooter(groupRowId);
                    } else {
                        this.dg.writeConsole('æªè·åå°groupRowIdã', 'error');
                    }
                }
            }
        });
        this.subscriptions.push(_s5);

        this.scrollToCurrentRowSubscription = this.dg.dgs.scrollToCurrentRow.pipe(
            delay(50),
        ).subscribe(() => {
            this.scrollToFirstSelectedRow();
            const scrollInfo = this.dg.dfs.getState().scrollInfo;
            this.dg.scrollInstance.scrollToLeft();
            if (this.dg.enableFilterRow && scrollInfo && scrollInfo.x) {
                this.dg.dgs.closeFilterPanel.emit();
            }
        });


        this.dg.dfs.checkRow$.pipe(
            takeUntil(this.destroy$)
        ).subscribe( () => {
            this.checkedRows = this.dg.dfs.getCheckeds();
            this.detectChanges();
        });

        this.dg.dfs.clearCheckeds$.pipe(
            takeUntil(this.destroy$)
        ).subscribe( () => {
            this.checkedRows =  this.dg.dfs.getCheckeds();
            this.detectChanges();
        });


        this.dg.dgs.toggleGroupRow$.pipe(
            takeUntil(this.destroy$)
        ).subscribe(({ expand, groupRowIndex }) => {
            this._toggleGroupRow(groupRowIndex, expand);
            setTimeout(() => {
                if (this.dgb.ps){
                    this.dgb.ps.update();
                }
            }, 10);
        });

        this.dg.dgs.toggleAllGroupRows$.pipe(
            takeUntil(this.destroy$)
        ).subscribe(({expand}) => {
            this.toggleAllGroupRows(expand);
            // setTimeout(() => {
            //     this.dg.scrollToTop();
            // });
            setTimeout(() => {
                if (this.dgb.ps){
                    this.dgb.ps.update();
                }
            }, 10)
        })
    }

    ngOnInit(): void {
        this.listenEvent();
    }

    ngOnDestroy() {
        this.unSubscriptions();
    }

    private _toggleGroupRow(groupRowIndex: number, expand: boolean = true) {
        if (groupRowIndex > -1) {
            const allRows = this.dg.getRows();
            const groupRows = allRows.filter(n => n[IS_GROUP_ROW_FIELD]);
            let gr = groupRows[groupRowIndex];
            if (!gr) {
                gr = allRows[groupRowIndex];
            }
            if (gr) {
                this.toggleGroupRow(gr, groupRowIndex, expand);
            }
        }
    }

    private toggleAllGroupRows(expand = true) {
        const groupRows = this.data.filter(n => n[IS_GROUP_ROW_FIELD]);
        groupRows.forEach(gr => {
            gr.expanded = expand;
            this.dg.dfs.setGroupRowsVisible(gr, expand, false);
        });

        this.cd.detectChanges();
        if (expand){
            this.dg.dfs.expandGroupRow.emit();
        } else {
            this.dg.dfs.collapseGroupRow.emit();
        }
    }



    private unSubscriptions() {
        this.destroy$.next();
        this.destroy$.complete();
        if (this.scrollToCurrentRowSubscription) {
            this.scrollToCurrentRowSubscription.unsubscribe();
            this.scrollToCurrentRowSubscription = null;
        }

        if (this.subscriptions && this.subscriptions.length) {
            this.subscriptions.forEach(n => n.unsubscribe);
            this.subscriptions = [];
        }
    }

    ngAfterViewInit() {
        this.ngZone.runOutsideAngular(() => {
            setTimeout(() => {
                if (!this.dg.nowrap) {
                    // this.dgb.updateRowHeight();
                    this.dg.calculateGridSize();
                }
            }, 100);
        });
    }

    getGroupColumn(field) {
        return this.dg.flatColumns.find(col => col.field === field);
    }

    private detectChanges() {
        if (!this.cd['destroyed']) {
            this.cd.detectChanges();
        }
    }

    trackByField(index, item) {
        return item.field;
    }

    isElementInViewport(el) {
        const container = this.dgb.ps.instance.element.getBoundingClientRect();
        const box = el.getBoundingClientRect();
        const top = (box.top <= container.height + container.top);
        const bottom = (box.bottom <= (container.bottom));

        return (top && bottom);
    }

    private _elementIntoView(tr: any, rowIndex: number) {
        if (tr) {
            if (tr.scrollIntoViewIfNeeded) {
                tr.scrollIntoViewIfNeeded(false);
            } else {
                tr.scrollIntoView({ block: 'nearest', inline: 'nearest' });
            }
        } else {
            const currentTrTop = rowIndex * this.dg.rowHeight;
            if (currentTrTop) {
                this.dg.scrollInstance.scrollToY(currentTrTop);
            }
        }
    }

    private scrollToFirstSelectedRow() {
        if (this.dg.selectedRow) {
            const rowIndex = this.dg.selectedRow.index;
            const rowId = TR_ID_PREFIX + this.dg.selectedRow.id;
            // const tr: any = document.querySelector(rowId);
            const tr: any = Utils.getTrDomById(rowId, document);
            if (rowIndex === 0) {
                if (this.dgb.ps && this.dgb.ps.instance) {
                    if (this.dgb.ps.instance.element.scrollTop === 0) {
                        return;
                    } else {
                        this.dg.scrollInstance.scrollToY(0);
                        return;
                    }
                }
            }

            const { top, bottom } = this.dgb.getVisibleRange();
            if (rowIndex === top || rowIndex === bottom) {
                this._elementIntoView(tr, rowIndex);
                return;
            } else {
                if (rowIndex > top && rowIndex < bottom && tr && this.isElementInViewport(tr)) {
                    return;
                }
            }

            this._elementIntoView(tr, rowIndex);

        }
    }


    toggleGroupRow(row, index, open, $event?: MouseEvent) {
        if ($event) {
            $event.stopPropagation();
        }
        row.expanded = open;
        this.dg.dfs.setGroupRowsVisible(row, open);
    }

    private mergeInfo(field: string, rowIndex: number) {
        return this.dg.dgs.getMergeInfo(rowIndex, field, this.dg.mergeCellInfo);
    }

    showCell(col: DataColumn, rowIndex: number) {
        const mergeInfo = this.mergeInfo(col.field, rowIndex);
        let show = true;
        if (mergeInfo) {
            show = mergeInfo.show;
        }

        return show;
    }

    getRowSpan(col: DataColumn, rowIndex: number) {
        const mergeInfo = this.mergeInfo(col.field, rowIndex);
        if (!mergeInfo) {
            return 1;
        }
        if (this.showCell(col, rowIndex)) {
            col.readonly = true;
            return mergeInfo.rowspan;
        } else {
            return 1;
        }
    }

    getGroupSummaryData(groupId: string) {
        const _data = this.dg.getRows();
        if (_data && _data.length) {
            const row = this.dg.getRows().find(n =>  n['__group_parent_id__'] === groupId && n['__group_footer__']);
            if (row) {
                return row.data;
            }
        }
        return null;
    }
}


