import { Subject } from 'rxjs';
import { Component, EventEmitter, Input, OnInit, Output, ElementRef, Renderer2, Injector, OnChanges, SimpleChanges, ViewChild, Optional, OnDestroy } from '@angular/core';
import { DatagridComponent } from '../../datagrid.component';
import { takeUntil } from 'rxjs/operators';

@Component({
    selector: 'smart-filter-toolbar',
    templateUrl: './smart-filter-toolbar.component.html',
})
export class SmartFilterToolbarComponent implements OnInit, OnChanges, OnDestroy {
    @Input() padding = '';
    @Input() height = 40;
    @Input() filterData = [];
    @Output() clearFilters = new EventEmitter();
    @Output() removeFilter = new EventEmitter();
    @Output() filterChange = new EventEmitter();

    @Output() resize = new EventEmitter();

    @ViewChild('farrisFilter') filtebar: any;
    sk = '';
    dg: DatagridComponent;
    destory$ = new Subject();
    constructor(private el: ElementRef, private render: Renderer2, private inject: Injector) { }

    ngOnInit(): void {
        this.dg = this.inject.get<DatagridComponent>(DatagridComponent);
        this.sk =  'smartfilter_' + this.dg.dgs.createConfigKey(this.dg.id);


        this.dg.dgs.clearFilter.pipe(takeUntil(this.destory$)).subscribe(() => {
            this.clearFilters.emit(null);
        });
    }

    ngOnChanges(changes: SimpleChanges) {
    }

    ngOnDestroy(): void {
        this.destory$.next();
        this.destory$.complete();
    }

    /** æ¡ä»¶ååäºä»¶ */
    onQuery($event) {
        const { data, filterItem } = $event;
        if (data && data.length) {
            const conditions = JSON.parse(data);
            if (conditions && conditions.length) {
                this.filterChange.emit({conditions, filterItem});
            }
        }
    }

    /** æ¸é¤æææ¡ä»¶ */
    onResetFilter($event) {
        this.clearFilters.emit($event);
    }

    /** ç§»é¤åä¸ªæ¡ä»¶ */
    onRemoveFilter($event) {
        this.removeFilter.emit($event);
    }

    /** å±å¼æå äºä»¶ */
    onResize($event) {
        setTimeout(() => {
            const smartFilterWrapper = this.el.nativeElement.querySelector('.f-filter-wrapper');
            if (smartFilterWrapper) {
                const h = smartFilterWrapper.offsetHeight;
                if (h) {
                    this.resize.emit(h + 6);
                }
            }
        });
    }

}
