import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'disableRow'})
export class DisableRowPipe implements PipeTransform {
    transform(rowData: any, rowIndex: number, disableRowFn: (row, index) => {}): any {
        if (disableRowFn) {
            return disableRowFn(rowData, rowIndex);
        }

        return false;
    }
}
