import { Optional, Pipe, PipeTransform } from '@angular/core';
import { DatagridFacadeService } from './../services/datagrid-facade.service';

@Pipe({name: 'formatGroupRow'})
export class FormatGroupRowPipe implements PipeTransform {

    constructor(@Optional() private dfs: DatagridFacadeService) {}


    transform(row: any, col: any, fn?: any): any {
        if (row) {
            let val = row['value'];
            if (!col && this.dfs) {
                col = this.dfs.getColumn(row.field);
            }

            if (col && col.formatter) {
                if (col.formatter.type === 'enum') {
                    const enumOpts: {valueField: string, textField: string, data: any} = col.formatter.options;
                    const { valueField, textField, data } = {...enumOpts};
                    const enumItem = data.find(item => item[valueField] === row['value']);
                    val = enumItem ? enumItem[textField] : '';
                }
            }

            return fn ? fn(row) : val;
        }

        return '';
    }
}
