import { Directive, Input, Injector, Renderer2, ElementRef, OnInit } from '@angular/core';
import { CUSTOM_GROUP_ROW_CLS } from '../../types/constant';
import { DatagridComponent } from './../../datagrid.component';

@Directive({
    selector: '[group-row]',
})
export class DatagridGroupRowDirective implements OnInit {
    @Input('group-row') groupRow;

    constructor(private injector: Injector, private render: Renderer2, private el: ElementRef, private dg: DatagridComponent) {

    }

    ngOnInit() {
        setTimeout(() => {
            if (this.dg && this.dg.groupStyler) {

                const tr = this.el.nativeElement;
                const _cls = Array.from(tr.classList).find((n: string) => n.indexOf(CUSTOM_GROUP_ROW_CLS) > -1);
                if (_cls) {
                    tr.classList.remove(_cls);
                }

                const styler = this.dg.groupStyler(this.groupRow);

                if (!styler) {
                    return;
                }

                if (!tr.id) {
                    tr.id = 'group-tr_' + this.groupRow.id;
                }

                const str = CUSTOM_GROUP_ROW_CLS + this.dg.idService.encrypt(JSON.stringify(styler));
               
                this.render.addClass(tr, str);
                this.dg.renderCustomStyle2(styler, tr, str);
                // this.dg.renderCustomStyle(styler, tr);
            }
        });
    }
}
